.filter-list {
    margin: 0;
    padding: 10px;
    list-style: none;
    text-align: center;
    border: 1px solid  rgb(188, 188, 188);
    border-radius: 999px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    li {
        display: inline-block;
        background: linear-gradient( -102deg, rgb(130,116,232) 0%, rgb(198,121,208) 100%);
        padding: 10px 31px;
        border-radius: 999px;
        color: #fff;
        text-transform: uppercase;
        font-weight: 600;
        font-size: 17px;
        margin-right: 10px;

        position: relative;
        z-index: 1;
        @include rt-trs(0.24s);
        cursor: pointer;
        @include breakpoint(lg) {
          padding: 14px 20px; }
        @include breakpoint(md) {
          margin: 5px; }
        &.active, &:hover {
            background-color: transparent;
            box-shadow: 0px 13px 40px 0px rgba(13, 21, 75, 0.2);
            &:after {
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                background-image: linear-gradient( 0deg, rgb(255,170,87) 0%, rgb(254,92,118) 100%);
                content: '';
                border-radius: 999px;
                @include rt-trs(0.24s);
                z-index: -1; } }
        &:last-child {
            margin-right: 0px; } } }


